<template>
  <div class="page page_layout_trading">
    <van-tabs v-model="tabActive">
      <van-tab title="挂单市场" name="transaction" to="/trade/transaction" replace />
      <van-tab title="区块链交易" name="crypto_trading" to="/trade/crypto_trading" replace />

      <van-tab
        v-if="!this.$root.userInfo.BusinessStatus"
        title="赚钱"
        name="earn_agreement"
        to="/trade/earn_agreement"
        replace
      />
      <van-tab v-else title="赚钱" name="earn" to="/trade/earn" replace />
    </van-tabs>
    <router-view
      :key="$route.path"
      :hasBottomBar="$root.bottomBarDisplay"
      :hasTopBar="$root.topBarDisplay"
      :isHome="$route.meta.isHome"
      class="ios-pd-tp ios-pd-bt"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      isAgreement: true,
      active: "",
    };
  },
  computed: {
    tabActive: {
      set() {
        this.active = this.$route.path.replace("/trade/", "");
      },
      get() {
        return this.active;
      },
    },
  },
  created() {
    this.$root.$emit("root:set-topbar", ["notices", "customer-service"]);
    this.$root.$emit("root:set-layout", { bottom: true });
  },
};
</script>

